* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
@import "../sass/_cdn.scss";
@import "../sass/_variables.scss";
@import "../sass/_mixins.scss";
@import "../sass/header.scss";
@import "../sass/download.scss";

html {
    font-family: 'Raleway', 'Cardo', sans-serif;
}
body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-color: #f6f4f4;
}


.header__container {
    width: 100%;
    margin: auto;
    padding: 0 15px;
    background-color: #fff;
    box-shadow: 10px 3px 10px 2px rgba(0, 0, 0, .1);
    }
.header__wrapper {
    width: 1170px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 40px 0;

}
.logo h2{
    @include h2;
    color: $coral;
}
.logo-title h2 {
    @include h2;
    color: $blue;
}
.date h2 {
    @include h2;
    color: $grey; 
    width: 50px;
}
.todo-list {
    position: relative;
    width: 1170px;
}

.todo-list-wrapper {
    margin: 30px auto;
    display: flex;
    justify-content: space-around;

}
.make-todo-column  {
    width: 360px;
}

.title {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 18px 0;
    box-shadow: 6px 3px 10px 2px rgba(0, 0, 0, .1);
    border-radius: 10px;
    background-color: #fff;
}
 h3 {
    @include h3;
    color: $black;   
}
.option {
    display: flex;
}
.option div{
    margin-left: 20px;
}

.option-plus-img img{
    width: 20px;
    height: 20px; 
    cursor: pointer;  
}
.delete p {
 color: $blue;   
}

.option-delete-img img{
    width: 20px;
    height: 20px;
    cursor: pointer;     
}
.in-progress-column {
    width: 360px;
}
.done-column {
    width: 360px;
}

.total {
    margin: 10px 0 0 0;
    padding: 10px 0;
    display: flex;
    justify-content: space-around;
    box-shadow: 6px 3px 10px 2px rgba(0, 0, 0, .1);
    border-radius: 10px;
    background-color: #fff;
    @include total;
}

.tasks-list {
    margin: 10px 0 0 0;
    padding: 15px;
    box-shadow: 6px 3px 10px 2px rgba(0, 0, 0, .1);
    background-color: #fff;
    border-radius: 10px;

}
.task-date {
    display: flex;
    justify-content: space-around;
}

.task {
    min-height: 200px;
    min-width: 330px;
    padding: 10px;
    background-color: #e6f1f1;
    border-radius: 10px;
    box-shadow: 6px 3px 10px 2px rgba(0, 0, 0, .1);
}

.task .task-wrapper {
    margin-left: 5px;
    min-height: 90px;
    width: 300px;
    position: relative;
}
.task .task-nd {
    min-height: 90px;
    display: flex;
    flex-direction: column;

}
.task-name {
    @include task-name;
    
}
.task .task-nd .task-descr{
    margin-top: 10px;
    font-size: 14px;
}
.task button {
    // position: absolute;
    box-sizing: border-box;
    height: 20px;
    width: 80px;
    bottom: 0;
    right: 0;
    border: none;
    background-color: #c468e8;
    color: $white;
    border-radius: 10px;
    cursor: pointer;  
}
.task hr {
    margin: 15px;
}
.task .delete {
    display: flex;
    justify-content: space-around;
}
.delete p {
    font-family: 'Raleway';
    font-style: bold;
    font-weight: 500;
    font-size: 17px;
   }


.option-delete {
    display: flex;
    margin-left: 20px;
}
.option-delete-img img{
    width: 20px;
    height: 20px;  
    cursor: pointer;   
}
